'use client'

import Navbar from "../../components/Navbar";
import { useTheme } from "../../context/ThemeContext";
import { FaRegMoon } from "react-icons/fa";
import { BsSun } from "react-icons/bs";
import CourseCarousel from "../../components/CourseCarousel";

export default function Home() {
  const { theme, toggleTheme } = useTheme();

  const courses = [
    { title: "PRESTAÇÃO DE SERVIÇOS", curr_module: "MÓDULO 12", image: "/images/ComprasIcons/example-1.png" },
    { title: "DROPSHIPPING", curr_module: "MÓDULO 04", image: "/images/ComprasIcons/example-1.png" },
    { title: "GOOGLE ADS", curr_module: "MÓDULO 10", image: "/images/ComprasIcons/example-1.png" },
    { title: "CHAT BOT", curr_module: "MÓDULO 01", image: "/images/ComprasIcons/example-1.png" },
    { title: "SUPORTE & COMUNIDADE", curr_module: "MÓDULO 09", image: "/images/ComprasIcons/example-1.png" },
    { title: "COMPRAS", curr_module: "MÓDULO 07", image: "/images/ComprasIcons/example-1.png" },
    { title: "PRESTAÇÃO DE SERVIÇOS", curr_module: "MÓDULO 12", image: "/images/ComprasIcons/example-1.png" },
  ];

  return (
    <>
      <div className={`relative w-full min-h-screen flex overflow-hidden ${theme === "dark" ? "bg-[#171717] text-[#D9D9D9] bg-overlay" : "bg-white text-[#6C6C6C]"}`}>
        <Navbar />

        <div className="ml-[72px] md:ml-[22%] w-full">
          <div className="sm:mx-4 md:mx-8 lg:mx-16">
            
            <CourseCarousel courses={courses} />

          </div>
        

          <button
            onClick={toggleTheme}
            className="fixed bottom-5 right-5 p-3 rounded-full shadow-lg"
          >
            {theme === "dark" ? ( <FaRegMoon className="text-white" size={24} />) : ( <BsSun className="text-[#888888]" size={24} />)}
          </button>

        </div>
      </div>
    </>
  )
}